body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

header{
  background-color: black;
  color:white;
  padding:20px 30px;
  display:flex;
  justify-content: space-between;
  font-size:30px;
  font-family:Arial, Helvetica, sans-serif;
  letter-spacing:2px ;
  align-items: center;
  justify-content: center;
}
.flex-center{
  display: flex;
  align-items: center;
  justify-content: center;

}
.QuizStart{
  text-align: center;
  padding-top: 200px;
}
.container{
  
  height: 80vh;
}
.quiz-start {
  flex-direction: column;
  gap: 20px;
}
.quiz-start button{
  background-color: black;
  color: white;
  padding: 5px 9px;
  border-radius: 3px;
}
.wrapper{
  border: 1px solid gray;
  border-radius: 3px;
  padding: 15px;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.options{
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.question{
  font-weight: bold;
}
.option{
  margin-right: 5px;
}
.wrapper button{
  background-color: black;
  color:white;
  padding: 7px;
  border-radius: 3px;
}
.result{
  text-align: center;
}
.result .text{
  line-height: 2;
}
.result button{
  background-color: black;
  color: white;
  padding: 3px 7px;
  border-radius: 3px;
  margin-top: 10px;

}